<template>
 <div :key="task.id" v-for="task in tasks">
   <Task @delete-task="$emit('delete-task', task.id)" :task="task" @mark-unmark="$emit('mark-unmark', task.id)" />
   </div>
</template>

<script>
import Task from './Task'
export default {
  name: 'Tasks',
  components: {
    Task
  },
  props: {
    tasks: Array
  },
  emits: ['delete-task', 'mark-unmark']
}
</script>
