<template>
    <form @submit="onSubmit" class="add-form">
    <div class="form-control">
      <label>Task</label>
      <input type="text" v-model="text" name="text" placeholder="Add Task" />
    </div>
    <div class="form-control">
      <label>Day & Time</label>
      <input
        type="text"
        v-model="day"
        name="day"
        placeholder="Add Day & Time"
      />
    </div>
    <div class="form-control form-control-check">
      <label>Set Reminder</label>
      <input type="checkbox" v-model="remainder" name="reminder" />
    </div>

    <input type="submit" value="Save Task" class="btn btn-block" />
  </form>
</template>

<script>
export default {
  name: 'AddTask',
  data () {
    return {
      text: '',
      day: '',
      remainder: false

    }
  },
  methods: {
    onSubmit (e) {
      e.preventDefault()
      if (!this.text) {
        alert('Add a Task')
        return
      }
      const newTask = {
        id: Math.floor(Math.random() * 100000),
        text: this.text,
        day: this.day,
        remainder: this.remainder
      }
      this.$emit('add-task', newTask)
    }
  }
}
</script>

<style scoped>
.add-form {
  margin-bottom: 40px;
}
.form-control {
  margin: 20px 0;
}
.form-control label {
  display: block;
  color: #09aa41;
}
.form-control input {
  width: 100%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
  color:  #37f841;
  background-color: black;
  border-color: rgba(0,0,0,0);

}
.form-control input text {
  box-shadow: 1px 1px 1.2px 1.2px #000000;
}
.form-control input:active {
    border-color: rgba(0,0,0,0);
     color:  #37f841;
  background-color: black;
}
.form-control-check {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.form-control-check label {
  flex: 1;
}
.form-control-check input {
  flex: 2;
  height: 20px;
  background-color: black;
}

.btn {
 color:  #37f841;
}

.btn:hover {
  background-color: black;
  color: #37f841;
}
</style>
