<template>
  <header>
    <h1>{{title}}</h1>
    <Button @toggle-add-task="$emit('toggle-add-task')"  :text="showAddTask ? 'Close' : 'Add Task'"
      :color="showAddTask ? 'red' : 'teal'" />
  </header>
</template>

<script>
import Button from './Button'

export default {
  name: 'Header',
  props: {
    title: String
  },
  components: {
    Button
  },
  emits: ['toggle-add-task']
}
</script>

<style scoped>
header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}
header h1{
   background: -webkit-linear-gradient(#00FF87, #60EFFF);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 250%;
}
</style>
