<template>
<div @dblclick="reminderMark(task.id)" :class="[task.remainder ? 'remainder':'', 'task']">

  <h3>{{task.text}}
 <i @click="onDelete(task.id)" class="fa fa-trash"></i></h3>
  <p>{{task.day}}</p>
  </div>
</template>

<script>
export default {
  name: 'Task',
  props: {
    task: Object
  },
  methods: {
    onDelete (id) {
      this.$emit('delete-task', id)
    },
    reminderMark (id) {
      this.$emit('mark-unmark', id)
    }
  }
}
</script>

<style scoped>
.fa {
  color: gray;
  transform: scale(0.9,0.9);
}
.fa:hover{
   transform: scale(1,1);
    box-shadow: 0.3px 0.3px 1px rgba(255,255,253,0.13);
    color: red;
}
.task {
  background-color: black;
  margin: 5px;
  padding: 10px 20px;
  cursor: pointer;
  color: #1DB954;
  font-size: 80%;
  box-shadow: 1px 1px 1.2px 1.2px #000000;

}
.task:hover {
    transform: scale(1.03,1.03);
    box-shadow: 1px 6px 15px rgba(255,255,253,0.13);

}
.task.remainder {
  border-left: 10px solid teal;
}
.task h3 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
</style>
